import React, { useEffect, useRef } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import './HomePage.css';

function HomePage() {
  const mountRef = useRef(null);
  const frameIdRef = useRef(null);
  const portalRef = useRef(null);
  const hoverMessageRef = useRef(null);

  useEffect(() => {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    const renderer = new THREE.WebGLRenderer({ antialias: true });

    renderer.setSize(window.innerWidth, window.innerHeight);
    mountRef.current.appendChild(renderer.domElement);

    const ambientLight = new THREE.AmbientLight(0x404040);
    scene.add(ambientLight);

    const pointLight = new THREE.PointLight(0xffffff, 1);
    camera.add(pointLight);
    scene.add(camera);

    const loader = new GLTFLoader();
    loader.load('./assets/models/home/scene.gltf', (gltf) => {
      scene.add(gltf.scene);
    }, undefined, (error) => {
      console.error(error);
    });

    loader.load('./assets/models/home/portal.glb', (gltf) => {
      portalRef.current = gltf.scene;
      gltf.scene.scale.set(0.15, 0.15, 0.15);
      gltf.scene.position.set(-0.7, 0.9, 0.8);
      scene.add(gltf.scene);
    }, undefined, (error) => {
      console.error(error);
    });

    camera.position.set(1, 1.2, 1);
    camera.lookAt(new THREE.Vector3(-2, 1.2, -2));

    const lamp = new THREE.PointLight(0xff0000, 1, 100);
    lamp.position.set(0, 2, 0);
    scene.add(lamp);

    const animate = () => {
      frameIdRef.current = requestAnimationFrame(animate);
      renderer.render(scene, camera);
    };
    animate();

    const handleWindowResize = () => {
      renderer.setSize(window.innerWidth, window.innerHeight);
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
    };
    window.addEventListener('resize', handleWindowResize);

    const raycaster = new THREE.Raycaster();
    const mouse = new THREE.Vector2();

    const onMouseMove = (event) => {
      event.preventDefault();
      mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
      raycaster.setFromCamera(mouse, camera);
      const intersects = raycaster.intersectObjects(scene.children, true);

      if (intersects.some(intersect => intersect.object === portalRef.current)) {
        renderer.domElement.style.cursor = 'pointer';
        if (hoverMessageRef.current) {
          hoverMessageRef.current.style.display = 'block';
          hoverMessageRef.current.style.left = `${event.clientX}px`;
          hoverMessageRef.current.style.top = `${event.clientY}px`;
        }
      } else {
        renderer.domElement.style.cursor = 'default';
        if (hoverMessageRef.current) {
          hoverMessageRef.current.style.display = 'none';
        }
      }
    };
    window.addEventListener('mousemove', onMouseMove);

    const onClick = (event) => {
      event.preventDefault();
      mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
      raycaster.setFromCamera(mouse, camera);
      const intersects = raycaster.intersectObjects(scene.children);

      if (intersects.some(intersect => intersect.object === portalRef.current)) {
        console.log('portal clicked');
        window.location.href = 'https://www.nestle-cereals.com/fr/cereales/chocapic';
      }
    };
    window.addEventListener('click', onClick);

    return () => {
      cancelAnimationFrame(frameIdRef.current);
      window.removeEventListener('resize', handleWindowResize);
      window.removeEventListener('mousemove', onMouseMove);
      window.removeEventListener('click', onClick);
      scene.traverse((object) => {
        if (object.isMesh) {
          object.geometry.dispose();
          if (Array.isArray(object.material)) {
            object.material.forEach((material) => disposeMaterial(material));
          } else {
            disposeMaterial(object.material);
          }
        }
      });

      renderer.dispose();

      function disposeMaterial(material) {
        Object.keys(material).forEach((prop) => {
          if (material[prop] && typeof material[prop].dispose === 'function') {
            material[prop].dispose();
          }
        });
        material.dispose();
      }
    };
  }, []);

  return (
    <section id='home' ref={mountRef}>
      <div ref={hoverMessageRef} id="hoverMessage" style={{display: 'none', position: 'absolute', pointerEvents: 'none'}}>
        Message de survol
      </div>
    </section>
  );
}

export default HomePage;